.insurance-wrapper{
    display: grid;
    grid-template-areas:"info list";
    grid-template-rows: auto 1fr;
    
    justify-content: center;
    align-items: flex-start;

    gap: 30px;
    padding: 30px 24px;
}

/* 선택한 자동차 보험 CSS */
.select-car-info{
    grid-area: info;

    display: flex;
    flex-direction: column;
    gap: 12px;

    width: 320px;

    border: 1px solid rgba(238, 238, 238, 1);
    border-radius: 6px;
    outline: none;
    padding: 13px 25px;
}

.car-image-detail{
    cursor: pointer;
}

/* 선택한 자동차 사진 CSS */
.select-car-info-image{
    display: flex;
    align-items: center;
    justify-content: center;

    border: 1px solid rgba(238, 238, 238, 1);
    overflow: hidden;
    border-radius: 6px;
    outline: none;
    padding: 13px;

    /* width: 200px; */
    height: 180px;
}

